/* global jQuery MktoForms2 */
/*
 * Wrap Marketo form fields to support two column layout
 */
(function ($, MktoForms2) {

	"use strict";

	$(document).on("alterFormValuesComplete", function () {
		$(".mktoFormRow.Email, \
			.mktoFormRow.FirstName, \
			.mktoFormRow.LastName, \
			.mktoFormRow.Company, \
			.mktoFormRow.Phone, \
			.mktoFormRow.Country, \
			.mktoFormRow.State, \
			.mktoFormRow.Company_Size_Range__c_account").wrapAll('<div class="fieldsWrapper" />');
	});

	MktoForms2.onFormRender(function () {
		console.log("a");
		setTimeout(function(){ 
			$(".mktoFormRow.State").detach().insertAfter($(".mktoFormRow.Country"));
		}, 0);
	});
}(jQuery, MktoForms2));
